import * as React from "react";
import { graphql } from "gatsby";
import { useTranslation, useI18next } from "gatsby-plugin-react-i18next";
import { Link } from "gatsby";
import Layout from "../components/Layout";
import Helmet from "react-helmet";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { StaticImage } from "gatsby-plugin-image";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

const Ampicilina = () => {
  const { t } = useTranslation();
  const i18n = useI18next();
  // const currentLanguage = i18n.language;

  return (
    <Layout>
      <main>
        <Helmet
          htmlAttributes={{
            lang: i18n.language,
          }}
        >
          <title>Health Supply</title>
          <meta
            name="keywords"
            content="medicinas, medicamentos, especialidades, oncologia, neurologia, cardiologia, reumatologia, hepatologia, ecuador"
          />
          <meta
            name="description"
            content="Health Supply, ofrece a pacientes el acceso a medicamentos no disponibles o difíciles de conseguir de forma rápida, segura y cumpliendo todas las normativas legales"
          />
        </Helmet>
        {/* <Menu /> */}
        <Container className="bg-light">
          <Row>
            <Col md={12}>
              <Breadcrumb>
                <Breadcrumb.Item href="#">Home</Breadcrumb.Item>
                <Breadcrumb.Item href="https://getbootstrap.com/docs/4.0/components/breadcrumb/">
                  Library
                </Breadcrumb.Item>
                <Breadcrumb.Item active>Data</Breadcrumb.Item>
              </Breadcrumb>
            </Col>
          </Row>

          <Row className="mt-4 mt-md-5 d-flex justify-content-center bg-white">
            <Col md={6} className="my-auto">
              <StaticImage
                alt="health supply mapa"
                src="../images/productos/dermatologia/medicamento2.png"
                className=""
                style={{ height: "100%", width: "100%" }}
                imgStyle={{ objectFit: "contain" }}
              />
            </Col>
            <Col md={5} className="my-auto">
              <h2 className="titulo-home mt-3 mt-md-0">
                ESPECIALIDAD DERMATOLOGÍA
              </h2>
              <p className="texto-home mt-4">
                Lorem ipsum, dolor sit amet consectetur adipisicing elit.
                Commodi ullam excepturi rem eveniet quae nemo consequatur eius
                assumenda sunt vel explicabo, nesciunt libero sint facere
                laborum odio! Aliquid, modi atque.
              </p>
            </Col>
          </Row>
          <Row className="g-4 py-4 py-md-5">
            <Col md={12}>
              <Tabs
                defaultActiveKey="home"
                id="uncontrolled-tab-example"
                className="mb-3"
              >
                <Tab eventKey="home" title="DESCRIPCIÓN">
                  <p>
                    LAS CÁPSULAS: son pequeños envases solubles generalmente
                    fabricados a base de gelatina en cuyo interior se halla la
                    dosis del fármaco que se administrará por vía oral.
                  </p>

                  <p>
                    Los comprimidos: son formas farmacéuticas sólidas,
                    generalmente redondas, con una dosificación exacta del
                    medicamento contenido en su interior.
                  </p>

                  <p>
                    La pastilla: es un término coloquial que se le da en a la
                    actualidad a las tabletas y se define como forma
                    farmacéutica sólida de dosificación unitaria, preparadas por
                    moldeo o compresión que contiene principio activo y
                    excipientes.
                  </p>

                  <p>
                    Las tabletas: son formas farmacéuticas sólidas de
                    dosificación unitaria, obtenidas por compresión mecánica de
                    granulados o de mezclas de polvos con uno o varios
                    principios activos, con la adición, en la mayoría de los
                    casos, de diversos excipientes
                  </p>
                </Tab>
                <Tab eventKey="profile" title="DETALLES - FICHA DE DATOS">
                  HOLA
                </Tab>
                <Tab eventKey="contact" title="DATOS DE CONTACTO">
                  HOLA
                </Tab>
              </Tabs>
            </Col>
          </Row>
          <Row className="d-flex justify-content-center py-3 py-md-4 bg-white">
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="mhra logo"
                  src="../images/mhra-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
            <Col md={1} className="d-none d-sm-block"></Col>
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="ema logo"
                  src="../images/ema-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
            <Col md={1} className="d-none d-sm-block"></Col>
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="fda logo"
                  src="../images/fda-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
          </Row>
        </Container>
      </main>
    </Layout>
  );
};

export default Ampicilina;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
